import * as dayjs from 'dayjs';

const today = dayjs();

// Creates a week of data from today
const upToDateCount = [
    {date: today},
    {date: today},

    {date: today.subtract(1, 'day')},
    {date: today.subtract(2, 'day')},

    {date: today.subtract(2, 'day')},
    {date: today.subtract(2, 'day')},
    
    {date: today.subtract(3, 'day')},

    {date: today.subtract(4, 'day')},
    {date: today.subtract(4, 'day')},
    
    {date: today.subtract(5, 'day')},
    
    {date: today.subtract(6, 'day')},
    {date: today.subtract(6, 'day')},
    {date: today.subtract(6, 'day')},
    
    {date: today.subtract(7, 'day')},
];

// Copies it for every weeks of last month
upToDateCount.push(
    ...upToDateCount.map(date =>({date: dayjs(date.date).subtract(1, "week")})),
    ...upToDateCount.map(date =>({date: dayjs(date.date).subtract(2, "week")})),
    ...upToDateCount.map(date =>({date: dayjs(date.date).subtract(3, "week")})),
);

// Adds incrmenetBy 1 for each item
upToDateCount.map(count=> count.incrementBy = 1 )

let pushUpsCounts = upToDateCount.map(count => ({...count, incrementBy : 10 }))
pushUpsCounts.pop();

